import React from 'react';
import Structure from '../../Structure';
import styled from 'styled-components';
import { colors } from '@components/typo';

const Title = styled.div`
    font-size: 16px;
    color: ${colors.defaultColor};
    margin-bottom: 10px;
    font-weight: 700;
`;

const Bold = styled.div`
    font-weight: bold;
`;

const Desc = styled.p`
    padding: 10px;
`;

export default function AboutUs() {
    return (
        <Structure aboutus>
            <Desc>
                Sojiare is a Birmingham, United Kingdom headquartered platform that verifies the validity of
                organizations, governments, institutions, emails, e-commerce and content from different sources.
            </Desc>
            <Desc>
                It uses AI to identify the authenticity of content sources and flags down scam-related content. Sojiare
                allows users to validate the sources of different business-related content and allows brands/companies
                to register their platforms as legitimate entities, helping them build trust with their audience.
            </Desc>
            <Title>Why Sojiare came to be</Title>
            <Desc>
                Now and then, people deal with content from brands, individuals, and even government institutions with
                no idea how to separate original from fake sources; some have unfortunately fallen victim to different
                forms of scams. Sojiare is established to avoid uncertainty, ensure online safety, justice, and equity.
            </Desc>
            <Desc>
                Sojiare works as a subordinate body of Justice Lab, the legal desk of Citizens' Gavel Foundation for
                Social Justice.
            </Desc>
            <Desc>
                With Sojiare, we are devoted to building that trust for brands, individuals and government bodies,
                making communication and transactions efficient and reliable.
            </Desc>
            <Title>Contact Us</Title>
            <Bold>hi@sojiare.com </Bold>
            <Bold>Birmingham, United Kingdom.</Bold>
            <Bold>United Kingdom Company House Reg No 13455210</Bold>
        </Structure>
    );
}
